import React, { Component } from 'react';
import './App.css';

class
App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      list: [],
      templist:[],
      noofelement: 100,
      speed:5,
      wid:25,
      animation:[]
    };
    //this.generate=this.generate.bind(this);
    //this.sort=this.sort.bind(this);
    this.animations=this.animations.bind(this);
  }
  componentDidMount(){
    this.generate();
  };

   generate(){
    var array=[];
    var array2=[];
    var x;
    for(let i=0;i<this.state.noofelement;i++){
      x=Math.floor(Math.random()*656+5);
      array.push(x);
      array2.push(x);
    }
    this.setState({list:array});
    this.setState({templist:array2});
    var width=this.state.noofelement;
    width=1200/width;
    this.setState({wid:width});
  };



  animations(){
    let sp=this.state.speed;
    let arr=this.state.animation;
    let j=0;
    for (let i = 0; i < (arr.length/2)*3; i++) {
      console.log(2222);
      const arrayBars = document.getElementsByClassName('bar');
      if(i%3<1){
        setTimeout(()=>{
          let barOneStyle = arrayBars[arr[j]].style;
          let barTwoStyle = arrayBars[arr[j+1]].style;

            barOneStyle.backgroundColor = "yellow";
            barTwoStyle.backgroundColor = "yellow";


        },i*sp);

      }else if(i%3<2){
        setTimeout(()=>{
          let barOneStyle = arrayBars[arr[j]].style;
          let barTwoStyle = arrayBars[arr[j+1]].style;
          const temp1=barOneStyle.height;
          const temp2=barTwoStyle.height;
          barOneStyle.height=temp2;
          barTwoStyle.height=temp1;

        },i*sp);


      }else{
        setTimeout(()=>{
          let barOneStyle = arrayBars[arr[j]].style;
          let barTwoStyle = arrayBars[arr[j+1]].style;
            barOneStyle.backgroundColor = "blue";
            barTwoStyle.backgroundColor = "blue";
            j++;
            j++;

        },i*sp);
      }
    }

  }


  run(){
    this.sort();
    this.animations();
  }

  sort(){
    console.log(2256665622);

    var ani=[];
    ani=this.state.animation;
    ani.length=0;
    let ar=this.state.templist;
    //let ara=this.state.templist;
    //ar.push(4);
    for (let i = 0; i < ar.length; i++) {
      for (let j = 0; j < ar.length-i-1; j++) {
        //console.log("YESS");
        if(ar[j]>ar[j+1]){
          ani.push(j);
          ani.push(j+1);
          let temp=ar[j];
          ar[j]=ar[j+1];
          ar[j+1]=temp;
        }
      }
    }


    this.setState({animation:ani});
    //this.setState({animation:ani});
    //let temp=this.state.animation;
    console.log(this.state.animation.length);
    //this.animations();
    //this.setState({list:array});


  }
  speedchange(){

    let s=this.state.speed;
    s=(s+5)%100;
    this.setState({speed:s});

  }
  rangechange(){
    let num=this.state.noofelement;
    num=(num)%100+10;
    this.setState({noofelement:num});

  }

  render() {

    return (
      <div>

        <div className="header">
        <h1 className="head">
        SORTING  VISUALIZER
        </h1>
        <ul>
        <li>
        <button type="button" onClick={()=>this.generate()}>GENERATE NUMBERS</button>
        </li>
        <li>

        <button type="button" onClick={()=>this.speedchange()}>SPEED CHANGE {this.state.speed}</button>

        </li>
        <li>
        <button type="button" onClick={()=>this.run()}>BUBBLE SORT</button>
        </li>
        <li>
        <button type="button" onClick={()=>this.rangechange()}>RANGE CHANGE {this.state.noofelement}</button>
        </li>
        </ul>
        </div>
        <div className="center">
          <div className="apparea">
            {this.state.list.map((item,idx) => (
              <div className="bar" key={idx} style={{width:`${this.state.wid}px`,height: `${item}px`}}  >  </div>
            ))}
          </div>

        </div>
      </div>

    );
  }
}

export default App;
